
.App-logo {
  pointer-events: none;
  width: 50px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  display: flex;
  flex-direction: row;
  align-items: center;
  font-size: 20px;
  color: white;
  position: sticky;
  top: 0;
  z-index: 2;
}

.App-link {
  color: #61dafb;
}
.content-box{
  margin: 16px;
}
.list-title {
  font-size: 20px;
  color: #222;
}
.comment-content {
  font-size: 18px;
  color: #222;
}
.APP-list{
  margin: 16px;
}
.comment-btn{
  margin-left: 16px;
  cursor: pointer;
}
.comment-btn:hover{
  color: #222;
}
.App-comment{
  margin: 16px 0;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
